* {
    margin: 0;
    padding: 0;
    font-size: 16px;
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'PT Serif', serif;
    font-size: 30px;
    font-style: normal;
    font-weight: bold;
    margin: 0;
}

p {
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: normal;
}

a {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    text-decoration: none;
    color: #fff;
}

address {
    font-style: normal;
}

.header {
    height: 940px;
    background-image: url(.././images/main-screen.jpg);
    background-size: cover;
    background-position: center;
}

.header__top {
    height: 81px;
    border-bottom: 1px solid #D9D9DE;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;
}

.header__logo {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
}

.header__logo-link {
    margin-right: 20px;
}

.header__logo-title {
    width: 210px;
    color: #fff;
    font-size: 20px;
    margin: 0;
}

.header__logo-modification {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
}

.header__information {
    display: -ms-flexbox;
    display: flex;
    position: relative;
}

.header__information-email {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    color: #fff;
    position: relative;
}

.header__information-email::before {
    content: " ";
    position: absolute;
    left: -20%;
    background-image: url(.././images/Email.png);
    width: 24px;
    height: 24px;
}

.header__information-phone {
    margin-right: 69px;
    font-weight: bold;
    font-size: 18px;
}

.header__information-phone::before {
    content: " ";
    position: absolute;
    left: -10%;
    background-image: url(.././images/Phone.png);
    width: 24px;
    height: 24px;
}

.header__menu {
    margin-top: 20px;
}

.header__menu-link {
    margin-right: 32px;
}

.header__menu-link:last-child {
    margin-right: 0;
}

.header__title {
    margin-top: 106px;
    margin-bottom: 0;
    color: #FFFFFF;
    font-size: 90px;
    width: 724px;
}

.header__text {
    font-size: 20px;
    line-height: 140%;
    color: #FFFFFF;
    margin-top: 8px;
}

.container {
    width: 1120px;
    margin: 0 auto;
}

button {
    background: transparent;
    color: #fff;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
}

.header__top-button {
    width: 186px;
    height: 44px;
    border-radius: 22px;
    border: 1px solid rgba(255, 255, 255, 0.5);
}

.header__button {
    width: 250px;
    height: 60px;
    margin-top: 94px;
    border-radius: 30px;
    border: 1px solid #FFFFFF;
    font-size: 18px;
}

.information {
    min-height: 792px;
    margin-top: 64px;
}

.information__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 32px;
}

.information__block {
    width: 256px;
    height: 200px;
    border-radius: 6px;
    border: 1px solid #D9D9DE;
}

.information__link {
    display: block;
    margin-top: 13px;
    margin-left: 19px;
    font-weight: bold;
    color: #000;
}

.directions {
    min-height: 582px;
    background-color: #e2f3d5;
}

.directions__title {
    text-align: center;
    padding: 44px 0 50px;
}

.directions__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding-bottom: 32px;
}

.directions__block {
    max-width: 256px;
}

.directions__subtitle {
    font-size: 16px;
    margin-top: 16px;
    line-height: 130%;
}

.offer {
    min-height: 1356px;
    border-bottom: 1px solid #D9D9DE;
    background: #efe3e3;
    border-bottom: #C4C4C4;
}

.offer__title {
    padding: 76px 0 40px;
}

.offer__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 32px;
}

.offer__link {
    display: block;
    width: 256px;
    height: 256px;
    border-radius: 6px;
    background-image: url(../images/offer/one.jpg);
    position: relative;
}

.offer__link-title {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    max-width: 218px;
    position: absolute;
    top: 10%;
    left: 10%;
}

.offer__link-subtitle {
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    font-weight: normal;
    position: absolute;
    left: 10%;
    bottom: 10%;
}

.offer__link:nth-child(2) {
    background-image: url(../images/offer/two.jpg);
}

.offer__link:nth-child(3) {
    grid-area: 1/3/3/5;
    width: 544px;
    height: 544px;
    background-image: url(../images/offer/3.jpg);
}

.offer__link:nth-child(3) .offer__link-title {
    width: 407px;
    top: 5%;
    left: 5%;
}

.offer__link:nth-child(3) .offer__link-subtitle {
    width: 218px;
    left: 5%;
    bottom: 5%;
}

.offer__link:nth-child(4) {
    background-image: url(../images/offer/four.jpg);
}

.offer__link:nth-child(5) {
    background-image: url(../images/offer/five.jpg);
}

.offer__link:nth-child(6) {
    background-image: url(../images/offer/six.jpg);
}

.offer__link:nth-child(7) {
    background-image: url(../images/offer/seven.jpg);
}

.offer__link:nth-child(8) {
    background-image: url(../images/offer/eight.jpg);
}

.offer__link:nth-child(9) {
    background-image: url(../images/offer/nine.jpg);
}

.offer__link:nth-child(10) {
    background-image: url(../images/offer/ten.jpg);
}

.offer__link:nth-child(11) {
    background-image: url(../images/offer/eleven.jpg);
}

.offer__link:nth-child(12) {
    background-image: url(../images/offer/twelve.jpg);
}

.offer__link:nth-child(13) {
    background-image: url(../images/offer/thirteen.jpg);
    font-size: 24px;
    vertical-align: middle;
}

.map {
    min-height: 1284px;
}

.map__title {
    text-align: center;
    padding: 70px 0 40px;
}

.map__wrapper {
    display: -ms-flexbox;
    display: flex;
    max-height: 609px;
    margin-top: 76px;
}

.map__block {
    max-width: 448px;
    margin-right: 32px;
}

.map__text {
    line-height: 140%;
    margin-top: 21px;
}

.map__video {
    font-size: 35px;
}

.gallery {
    min-height: 766px;
    background: #deeffc;
}

.gallery__title {
    padding: 44px 0 59px;
}

.gallery__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 32px;
}

.recreation {
    min-height: 1049px;
}

.recreation__title {
    font-weight: bold;
    font-size: 44px;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    padding: 80px 0 45px;
}

.recreation__wrapper {
    display: -ms-flexbox;
    display: flex;
    position: relative;
    border-bottom: 1px solid #D9D9DE;
}

.recreation__img:nth-child(2) {
    position: relative;
    top: -19%;
    left: 23%;
}

.recreation__text {
    max-width: 736px;
    margin: 0;
}

.recreation__facts {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 41px 0 85px;
}

.recreation__subtitle {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
}

.recreation__modification {
    font-family: 'PT Serif', serif;
    font-style: normal;
    font-weight: bold;
    font-size: 58px;
    line-height: 130%;
    color: #64BD22;
}

.reservation {
    min-height: 552px;
    background: #F5F5F7;
}

.reservation__title {
    text-align: center;
    padding: 48px 0 51px;
}

.reservation__form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 832px;
    margin: 0 auto;
}

.reservation__form-information {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}

.reservation__form-wrapp {
    display: -ms-flexbox;
    display: flex;
    margin-top: 24px;
}

.reservation__form-date {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    margin-right: 30px;
}

.reservation__form-submit {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    margin-top: 30px;
}

.reservation__information {
    width: 256px;
    height: 44px;
    background: #FFFFFF;
    border: 1px solid #D9D9DE;
    margin-right: 32px;
    padding-left: 14px;
}

.reservation__information:nth-child(3n) {
    margin-right: 0;
}

.reservation__information:nth-child(n+4) {
    margin-top: 24px;
}

.reservation__button {
    width: 297px;
    height: 60px;
    font-size: 18px;
    text-align: center;
    background: #0061D9;
    border: transparent;
    border-radius: 30px;
    color: #fff;
    margin-right: 140px;
}

.reservation__checkbox {
    margin-right: 16px;
    height: 26px;
    width: 26px;
}

.reservation__date {
    width: 153px;
    height: 44px;
    border: 1px solid #D9D9DE;
    padding-left: 14px;
}

.reservation__date:nth-child(2) {
    margin-top: 24px;
}

.reservation__textarea {
    width: 649px;
    height: 112px;
    padding: 11px 0 0 14px;
}

.reservation__link {
    -webkit-text-decoration-line: underline;
    text-decoration-line: underline;
    color: #76767A;
    font-size: 16px;
}

.footer {
    min-height: 331px;
    background: #202020;
}

.footer__top {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 39px 0 26px;
    border-bottom: 1px solid #FFFFFF;
}

.footer__top-title {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    color: #FFFFFF;
}

.footer__top-link {
    font-size: 16px;
    margin-top: 17px;
    opacity: 0.8;
}

.footer__block {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    line-height: 130%;
}

.footer__bottom {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: baseline;
    align-items: baseline;
    padding: 32px 0 45px;
}

.footer__bottom-block:last-child {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}

.footer__bottom-title {
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    line-height: 130%;
}

.footer__bottom-text {
    color: #FFFFFF;
    font-size: 14px;
    line-height: 130%;
    opacity: 0.8;
    margin-top: 7px;
}

.footer__bottom-link {
    font-size: 14px;
    text-decoration: underline;
}

.footer__bottom-link:last-child {
    opacity: 0.8;
    font-size: 12px;
    text-decoration: none;
    margin: 12px 0 0 100px;
}

.footer__bottom-logo {
    margin-left: 14px;
}

@media only screen and (min-width: 736px) and (max-width: 1087px) {
    .container {
        width: 672px;
    }

    .header {
        padding: 18px 32px 24px;
    }

    .header__information-email {
        display: none;
    }

    .header__information-phone {
        display: none;
    }

    .header__title {
        font-size: 64px;
    }

    .header__text {
        font-size: 18px;
    }

    .header__button {
        font-size: 16px;
    }

    .information__wrapper {
        grid-template-columns: 1fr 1fr;
    }

    .information__block {
        width: 320px;
        height: 250px;
    }

    .information__img {
        width: 320px;
        height: 190px;
    }

    .directions__wrapper {
        grid-template-columns: 1fr 1fr;
        grid-gap: 22px 32px;
    }

    .directions__block {
        max-width: 318px;
    }

    .offer__wrapper {
        grid-template-columns: 1fr 1fr;
    }

    .offer__link {
        width: 320px;
        height: 320px;
        background-repeat: no-repeat;
        background-size: contain;
    }

    .offer__link:nth-child(3) {
        grid-area: 3/1/3/3;
        background-position: center center;
        width: 671px;
        height: 671px;
    }

    iframe {
        width: 672px;
        height: 500px;
    }

    .map__wrapper {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
        flex-direction: column;
    }

    .map__block {
        max-width: 672px;
    }

    .gallery {
        padding-bottom: 40px;
    }

    .gallery__wrapper {
        grid-template-columns: 1fr 1fr;
    }

    .gallery__image {
        width: 320px;
        height: 232px;
    }

    .recreation__wrapp {
        display: none;
    }

    .recreation__text {
        line-height: 140%;
    }

    .recreation__facts {
        padding-bottom: 40px;
    }

    .recreation__subtitle {
        font-size: 14px;
    }

    .recreation__modification {
        font-size: 43px;
    }

    .reservation__information {
        width: 672px;
    }

    .reservation__information:nth-child(n+2) {
        margin-top: 14px;
    }

    .reservation__textarea {
        width: 496px;
    }
}

@media only screen and (min-width: 1088px) and (max-width: 1280px) {
    .container {
        width: 1024px;
    }

    .information__wrapper {
        grid-template-columns: 1fr 1fr 1fr;
    }

    .offer__link {
        width: 232px;
        height: 232px;
    }

    .offer__link:nth-child(3) {
        width: 494px;
        height: 494px;
    }

    iframe {
        width: 1024px;
    }

    .gallery__image {
        width: 320px;
    }
}
